import './SignUp.css';
import { Link } from 'react-router-dom';
import application from '../../firebase';
import { getAuth, createUserWithEmailAndPassword } from 'firebase/auth';
import { useState } from 'react';

function SignUp() {

  //User Authentication
  const auth = getAuth(application);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  function registration() {
    createUserWithEmailAndPassword(auth, email, password)
      .then((userCredential) => {
        const user = userCredential.user;
        console.log(user);
      })
      .catch((error) => {
       const errorMessage = error.message;
       alert(errorMessage);
      })
  }

  return (
    <>
      <Navigation/>

      <main>
        <section className='middlebar'>
          <p className='warning'>You don't have an account yet?<br/>Create one right now!</p>

          <div className='component-login'>

            <nav className='leftlogin'>

              <form action={'/store.parrotcloud/store?'}>
                <label>E-mail</label>
                <input type={'email'} required placeholder='Email' onChange={(e) => setEmail(e.target.value)}/>

                <label>Password</label>
                <input type={'password'} required placeholder='Password' onChange={(e) => setPassword(e.target.value)}/>

                <p>By singin up you agree with the terms of privacy and our coockie police.</p>

                <button onClick={registration}>Create Account</button>
                <br/>
                <p>Already have an account? <Link to='/store.parrotcloud/login'>Login.</Link></p>
              </form>

            </nav>
            <nav className='rigthlogin'>
              <p>
                You can signin up using
              </p>
            </nav>
          </div>
        </section>
      </main>
    </>
  );
}

const Navigation = () => {
  return (
      <header>
          <nav className="upheader">
              <span>
                  <Link to='/store.parrotcloud/' className='logo'>ParrotCloud_</Link>  
              </span>
              <span>
                  <Link to='/store.parrotcloud/login' className='login'>Sign In</Link>
              </span>
          </nav>
      </header>
  );
}

export default SignUp;